@use "../../styles/remConverter" as r;
@use "../../styles/variables" as v;

.sessionInfoContainer {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--theme-sessioninfo-background);
    padding: r.rem(10px) r.rem(20px);
    border-radius: r.rem(10px);
    font-family: v.$primary-font-light;
    cursor: pointer;
    @media only screen and (max-width: v.$medium-size-devices) {
        padding: r.rem(5px) r.rem(10px);
        column-gap: r.rem(15px);
    }

    @media only screen and (max-width: v.$small-size-desktop-devices) {
        flex-direction: column;
        row-gap: r.rem(10px);
    }
}

.break {
    cursor: default;
    background: repeating-linear-gradient(-45deg,
            var(--theme-session-break-bg1), var(--theme-session-break-bg2) 5px,
            var(--theme-session-break-bg3)0, var(--theme-session-break-bg3) 10px);

    @media only screen and (max-width: v.$medium-size-devices) {

        background: repeating-linear-gradient(-45deg,
                var(--theme-session-break-bg1), var(--theme-session-break-bg2) 3px,
                var(--theme-session-break-bg3)0, var(--theme-session-break-bg3) 8px);
    }
}