@use "../../styles/variables" as variable; 
@use "../../styles/remConverter" as function;

.eventDates{
  width: 20.5%;
  height: function.rem(25px);
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: variable.$primary-font-light;
  font-size: small;
  padding: 0 6px;
  position: absolute;
  top: 63%;
  right: 32.5%;
  border-radius: 15px;
  background-image: linear-gradient(to right, #e9a680 0%, #cb4444 50%, #064cb5 100%);
  color: variable.$white;
  cursor: pointer;
  @media  screen and (max-width : variable.$small-size-desktop-devices) {
    width: 23%;
    right: 29.5%;
  }
  // @media screen and (max-width : variable.$extra-large-size-devices) {
  //   width: 26%;
  // }
}
.selectedDate{
    width: function.rem(18px);
    height: function.rem(18px);
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: variable.$white;
    border-radius: 50%;
    color: variable.$black;
} 