@use  "../../../styles/remConverter" as r;
@use  "../../../styles/variables" as v; 
.eventTimeline{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    min-width: 9rem;
    font-size: r.rem(14px);
    font-family: v.$primary-font-light;
    @media only screen and (max-width: v.$medium-size-devices) {
        font-size: r.rem(12px);
    }
    .separator {
        margin: 0 0.3rem;
    }
}
.category{
  display: flex;
  column-gap: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.25rem;
  .screen, .techTalk, .solutionSpace{
    border-radius: 20px;
    padding: 0.1rem 0.8rem;
    font-size: 0.75rem;
  }
  .screen{
    background-color: v.$stage-color;
    border: 1px solid v.$stage-border-color;
  }
  .techTalk{
    background-color: v.$tech-talk-color;
    border: 1px solid v.$tech-talk-border-color;
  }
  .solutionSpace{
    background-color: v.$solution-space-color;
    border: 1px solid v.$solution-space-border-color;
  }
}