@use  "../../styles/variables" as variable;  
@use "../../styles/remConverter" as function;
.calendarItems{
    height: function.rem(25px);
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: variable.$primary-font-light;
    font-size: function.rem(14px);
}

.faded{
    width: function.rem(18px);
    text-align: right;
   opacity: 0.2;
}

.notFaded{
    width: function.rem(18px);
    text-align: right;
}