@use '../../styles/variables' as variable;
@use '../../styles/remConverter' as r;

.scheduleWrapper {
  width: 100%;
  font-family: variable.$primary-font-bold;
  scroll-margin: 2rem;
}

h3{
  @media screen and (max-width: variable.$large-size-devices) {
    margin-bottom: 1rem !important;
    }
  
}
.note{
  font-family:variable.$primary-font-regular;
  font-size: 0.75rem;
  font-style: italic;
  @media screen and (max-width: variable.$large-size-devices) {
   text-align: center;
   margin-bottom: 1rem;
   }
}
.leftContainer {
  width: 28%;
  padding: r.rem(15px) 0 r.rem(15px) 0;

  @media screen and (min-width: variable.$large-size-devices) and (max-width: 1340px){
   width: 41%;
  }
  @media screen and (min-width: 1341px) and (max-width: 1500px){
    width: 39%;
   }
  @media screen and (min-width: 1501px) and (max-width: 1600px){
    width: 37%;
   }
   @media screen and (min-width: 1601px) and (max-width: 1700px){
    width: 30%;
   }
  @media screen and (max-width: variable.$large-size-devices) {
    display: none;
  }

  // @media screen and (min-width: variable.$large-size-devices) and (max-width: variable.$extra-large-size-devices) {
  //   width: 35%;
  // }
  // @media screen and (max-width: variable.$large-size-devices) {
  //   width: auto;
  //   padding: 0;
  // }
}

.rightContainer {
  width: 75%;
  padding: r.rem(15px);

  // @media screen and (max-width : variable.$small-size-desktop-devices) {
  //     width: 100%;
  //     padding: 0;
  // }

  // @media screen and (min-width: variable.$large-size-devices) and (max-width: variable.$extra-large-size-devices) {
  //   width: 65%;
  // }
  
  @media screen and (max-width: variable.$large-size-devices) {
    width: 100%;
  padding: 0;
  }
}

.containerWrapper {
  display: flex;
  gap: 1rem;

  @media screen and (max-width: variable.$small-size-desktop-devices) {
    justify-content: center;
  }
}
