@use "../../styles/remConverter" as r;
@use "../../styles/variables" as v;

.agendaListContainer {

    .agendaContainer {
        display: flex;
        column-gap: r.rem(15px);
        margin-bottom: r.rem(20px);

        @media screen and (max-width: v.$large-size-devices ) {

            flex-direction: column;
            align-items: flex-start;
            row-gap: r.rem(10px);
        }

        .agendaRightContainer {
            width: 100%;
            background-color: var(--theme-page-background);
            padding: 0.25rem r.rem(20px);
            padding-bottom: 0.935rem;
            border-radius: r.rem(10px);

            @media screen and (max-width: v.$large-size-devices ) {
              width: 100%;
              padding : 0;
          }



            .sameTimelineSessions {
                background-color: var(--theme-agenda-background);
                // margin-bottom: r.rem(15px);
                margin-top: 0.9375rem;
                border-radius: r.rem(10px);
                opacity: 1;

            }

            @media only screen and (max-width: v.$medium-size-devices) {
                padding: r.rem(5px) r.rem(10px);
            }

        }


    }


}