@use '../../styles/variables' as var;

.eventInfoContainer {
  width: var.$event-info-comtainer;
  padding: 1.25rem 0.75rem;
  background-color: var(--theme-event-info-bg) ;
  display: flex;
  column-gap: 16px;
  color: var(--theme-color) ;
  border-radius: 10px;
  @media only screen and (max-width: var.$small-size-devices) {
     width: 16rem;
  }
  .theme {
    width: 1.25rem;
    height: 1.25rem;
    background-image: var.$color-gradient;
    border-radius: 100%;
    margin-top: 0.125rem;
  }
  .eventInfo {
    width: 90%;
    .eventTitle {
      line-height: 26px;
      font-family: var.$primary-font-medium;
      font-size:1.2rem;
    }
    
    .eventCategory {
      margin-top: 0.375rem;
      margin-bottom: 0.375rem;
      font-family: var.$primary-font-regular;
      font-size: 0.9rem;
      font-style: italic;
    }
    .eventDuration {
      font-size: 0.9rem;
      font-family: var.$primary-font-regular;
      margin-bottom: 0.625rem;
    }
    .screen{
      font-size: 0.9rem;
      font-family: var.$primary-font-regular;
      margin-bottom: 0.625rem;
    }
    .speakers {
      font-size: 0.9rem;
      font-family: var.$primary-font-regular;
    }
    .speaker {
      display: flex;
      column-gap: 16px;
      padding-top: 0.625rem;
      align-items: center;
      .speakerName {
        font-family: var.$primary-font-regular;
        font-size: 0.9rem;
        flex: 1;
      }
      .speakerImageContainer {
        width: var.$speaker-image;
        height: var.$speaker-image;
        .speakerImage {
          width: 100%;
          height: 100%;
          border-radius: 100%;
          aspect-ratio: 1;
        }
      }
    }
  }
}

