@use '../../styles/variables' as style;
@use '../../styles/remConverter' as converter;
.eventDetailsContainer {
  width: 100%;
  display: inline-block;
  font-family: style.$primary-font-regular;
  margin-top: 1.125rem;
  padding: 1.2rem;
  border-radius: 0.9375rem;
  background-color: var(--theme-page-background);

  .eventDetailsHeader {
    display: flex;
    justify-content: center;
    .title {
      font-size: converter.rem(18px);
      font-family: style.$primary-font-medium;
    }
  }
  .eventDetailsContent {
    font-size: small;
    .eventsPerDay {
      .eventDate {
        text-align: center;
        font-family: style.$primary-font-medium;
        font-size: converter.rem(16px);
      }
      .eventTimeline {
        .event {
          margin: 0.5rem 0;
          display: flex;
          flex-direction: row;
          align-content: center;
          justify-content: space-between;
          align-items: center;
          .eventTitle {
            flex: 1;
            margin-left: 0.75rem;
            font-size: 0.92rem;
          }
          .eventTiming {
            display: flex;
            flex: 1;
            flex-wrap: wrap;
            .eventStartTime,
            .eventEndTime {
              display: inline-block;
              font-size: 0.86rem;
              word-break: keep-all;
              text-align: left;
            }
            .timeSeparator {
              margin: 0 0.15rem;
            }
          }
        }
      }
    }
  }
}
