@use "../../../styles/remConverter" as r;
@use "../../../styles/variables" as v;

.sessionInfoLeftContainer {
    width: 60%;

    .sessionTitle {
        width: 100%;
        font-size: 1rem;
        font-family: v.$primary-font-light;
        margin-bottom: r.rem(5px);
    }

    .link {
        cursor: pointer;
    }
    @media only screen and (max-width: v.$small-size-desktop-devices) {
        width: 100%;
    }
}

.sessionInfoRightContainer {
    width: 40%;

    .sessionSpeakersContainer {
        display: flex;
        // flex-direction: row-reverse;
        justify-content: flex-end;
        flex-wrap: wrap;
        row-gap: r.rem(5px);

        img {
            width: r.rem(40px);
            height: r.rem(40px);
            margin: 0 r.rem(2px);
            border-radius: 50%;
            object-fit: cover;
            object-position: top;
        }
    }
    @media only screen and (max-width: v.$small-size-desktop-devices) {
        width: 100%;

        .sessionSpeakersContainer {
            flex-direction: row;
            justify-content: flex-start;
            margin-bottom: r.rem(5px);
        }
    }
}