@use "../../../styles/remConverter" as r;
@use "../../../styles/variables" as v;

.AgendaDateContainer {
    display: flex;
    flex-direction: column;
    align-items: center;

    .AgendaDay {
        font-family: v.$primary-font-light;
        font-size: r.rem(18px);

    }

    .AgendaFullDate {
        display: none;
    }

    .AgendaDate {
        width: v.$agenda-date;
        height: v.$agenda-date;
        line-height: v.$agenda-date;
        border-radius: 50%;
        background: v.$color-gradient;
        text-align: center;
        font-size: r.rem(22px);
        color: v.$white;
        font-family: v.$primary-font-medium;
    }

    @media screen and (max-width: v.$large-size-devices ) {
        & {
            .AgendaDay {
                display: none;
            }

            .AgendaFullDate {
                display: block;
                font-size: r.rem(18px);
                font-family: v.$primary-font-medium;
            }

            .AgendaDate {
                display: none;
            }
        }
    }
}